.sites-button {
  background: #FF6908;
  font-size: 1.25rem;
  font-weight: bold;
  box-shadow: 0 -2px 0 rgba(0,0,0,0.2) inset;
  border-radius: 3px;
  border: none;
}
.button.white {
  border-color: $white;
  color: $white;
  &:hover {
    border-color: $white;
    background: $white;
    color: $black;
  }
}
